/**
    
 */
*,
::before,
::after {
  box-sizing: border-box;
}

/**
         
 */
ul.class-name,
ol.class-name {
  padding-left: 0;
}

/**
     body      
 */
body,
blockquote.class-name,
figure.class-name {
  margin: 0;
}

/**
         
 */
h1.class-name,
h2.class-name,
h3.class-name,
h4.class-name,
h5.class-name,
h6.class-name,
p.class-name,
ul.class-name,
ol.class-name,
dl.class-name {
  margin-block: 0;
}

dd.class-name {
  margin-left: 0;
}

fieldset.class-name {
  margin-left: 0;
  padding: 0;
  border: none;
}

/**
        
 */
ul.class-name {
  list-style: none;
}

address.class-name {
  font-style: normal;
}

/**
      ,
        
 */
p {
  --paragraphMarginBottom: 24px;
  margin-block: 0;
}

/**
        ,
         
 */
p:not(.class-name):not(:last-child) {
  margin-bottom: var(--paragraphMarginBottom);
}

/**
       
 */
img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

/**
       
 */
input,
textarea,
select,
button {
  font: inherit;
}

html {
  height: 100%;
  scrollbar-gutter: stable;
}

/**
   
 */
html,
:has(:target) {
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  line-height: 1.5;
}

/**
          DevTools
 */
a.class-name {
  display: inline-flex;
}

/**
  -    
 */
button,
label {
  cursor: pointer;
}

/**
      svg-
 */
[fill]:not(
  [fill="none"],
  [fill^="url"]
) {
  fill: currentColor;
}

/**
      svg-  stroke
 */
[stroke]:not(
  [stroke="none"],
  [stroke^="url"]
) {
  stroke: currentColor;
}

/**
          svg-
 */
svg * {
  transition-property: fill, stroke;
}

/**
       'collapse' 
 */
table {
  border-collapse: collapse;
  border-color: currentColor;
}

/**
        ,
      
 */
@media (prefers-reduced-motion: reduce) {
  *,
  ::before,
  ::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}